import request from '@/utils/request'

// 
export const getDataVTypeSelector = () => {
  return request({
    url: '/api/System/DictionaryData/b1474890faaa47e1858ce7b593c8ffd7/Data/Selector',
    method: 'GET'
  })
}

// 
export const createDataV = data => {
  return request({
    url: '/api/DataV/Data',
    method: 'POST',
    data
  })
}

// 
export const updateDataV = (data, id) => {
  return request({
    url: `/api/DataV/Data/${id}`,
    method: 'PUT',
    data
  })
}

// 
export const getDataVInfo = id => {
  return request({
    url: `api/DataV/Data/${id}`,
    method: 'GET',
  })
}

// 
export const savaScreenShot = data => {
  return request({
    url: '/api/DataV/Images/screenShot/Uploader',
    method: 'POST',
    headers: {
      "Content-Type": "multipart/form-data"
    },
    data
  })
}


// 
export const getMapSeletor = () => {
  return request({
    url: '/api/DataV/Map/Selector',
    method: 'GET'
  })
}

// 
export const getDataInterfaceSelector = () => {
  return request({
    url: '/api/DataInterface/Selector',
    method: 'GET'
  })
}

// 
export const getDataInterfaceResponse = id => {
  return request({
    url: `/api/DataInterface/${id}/Actions/Response`,
    method: 'GET'
  })
}

// 
export const getImgList = type => {
  return request({
    url: `/api/DataV/Images/${type}`,
    method: 'GET',
  })
}